form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .admin-label {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    margin-bottom: 10px;
  }
  
  .admin-input {
    width: 300px;
    height: 30px;
    border: none;
    border-radius: 3px;
    margin-left: 10px;
    padding: 5px;
  }
  
  .admin-button {
    width: 100px;
    height: 30px;
    border: none;
    border-radius: 3px;
    background-color: #2196f3;
    color: white;
    margin-top: 20px;
  }
  